body{margin:0 auto;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.header{margin:0 auto;text-align:center}.nav{display:flex;justify-content:center;list-style-type:none;background-color:#79745C}.nav-item{padding:1.5em}

.cafe-image{position:relative;border:1px black solid;height:100%;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}img{object-fit:cover;width:100%;height:100%}.intro-blurb{position:absolute;color:whitesmoke;bottom:37.5%;width:100%}@media only screen and (max-device-width: 1366px){.cafe-image{background-attachment:scroll}}

.clock-section{background-color:#E6F8B2}


#ticket-btn{margin-bottom:1em}

.ticket-status{background-color:greenyellow;border:1px solid black}

.footer{display:flex;flex-wrap:wrap;justify-content:center;background-color:#709176}.footer-item{padding:0.5em}

.App{text-align:center}

